@use 'base/mixins' as m;
@use 'base/variables' as v;

.instructions{
    margin-top: 5rem;

    &__wave{
        
    }
    &__title{
        text-align: center;
        color: v.$white;

        &--span{
            font-size: 1.8rem;
            display: block;
        }
    }
   
    &__content{
        background-color: v.$primary;
        margin-top: -1px;
    }
    &__grid{
        @include m.tablet {
            @include m.grid(3, 2rem)
        }
    }
    &__img{
        transition: 0.3s;
        &:hover{
            transform: rotate(-4deg) scale(1.2);
        }
    }
}