@use 'base/mixins' as m;
@use 'base/variables' as v;

.shops{
    display: flex;
    flex-direction: column;
    justify-content: center;


    &__grid{
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
    }
    &__title{
        color: v.$secondary;
        margin: 2rem 0 0 0;
    }
    &__text{
        margin-bottom: 1rem;
    }
    &__title,
    &__text{
        text-align: center;
    }

    &__link{
        transition: 0.5s;
        &:hover{
            transform: scale(1.1);
        }
    }

}