@use 'base/mixins' as m;
@use 'base/variables' as v;

.testimonial{
    background-color: v.$secondary;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 2rem;
    color: v.$white;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include m.desktop {
        margin-bottom: 0;
        &:nth-child(1){
            grid-column: 1/5;
        }
        &:nth-child(2){
            grid-column: 1/5;
        }
        &:nth-child(3){
            grid-column: 5/6;
            grid-row: 1/3;
        }

        &:nth-child(4){
            grid-column:1/6 ;
        }

    }

    &__header{
        display: flex;
        gap: 1rem;
        align-items: center;
    }

    &__img{
        flex-basis: 10rem;
        border-radius: 50%;
        overflow: hidden;
    }
    &__author{
        color: v.$white;
        font-weight: bold;
        font-size: 3rem;
        margin: 0.5rem 0;
    }
    &__text{
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0;
        padding-left: 4rem;
        position: relative;

        &::before{
            content: '"';
            font-size: 10rem;
            position: absolute;
            top: -4rem;
            left: 0;
            opacity: 0.2;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            
        }
    }
}
