@use 'base/mixins' as m;
@use 'base/variables' as v;

.form{

    &__grid{
        @include m.tablet {
            @include m.grid(2, 2rem) ;
        }
    }

    &__cont{
        background-color: v.$primary;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    &__title{
        color: v.$white;
        text-align: center;
    }

    &__field{
        margin-bottom: 1rem;
    }
    &__label{
        display: block;
        text-transform: uppercase;
        color: v.$white;
        margin: 1rem 0;
    }
    &__input{
        width: 100%;
        padding: 1.5rem;
        border: none;
    }
    &__submit{
        margin-top: 2rem;
        @include m.btn(v.$secondary, v.$white);
        text-transform: uppercase;
        @include m.tablet {
            width: 100%;
        }

    }

    &__img--container{
        margin-top: 2rem;
        display: flex;
        justify-content: center;
    }
    &__img{
        width: 30rem;
        @include m.desktop {
            width: 100%;
        }
    }
    

}

