@use 'base/mixins' as m;
@use 'base/variables' as v;

.downloads{
    background-color: v.$white;
    &__grid{
        display: flex;
        flex-direction: column-reverse;
        padding: 5rem 0;

        @include m.tablet {
            @include m.grid(2, 5rem) ;
        }
    }
    &__img{
        margin: 5rem auto 0 auto;
        width: 50%;
        transition: 0.3s;
        @include m.tablet {
            margin-top: 0;
        }

        &:hover{
            transform: rotate(4deg) scale(1.2) ;
        }
    }
}