body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #eceeef;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
  transition: 0.3s;
}
a:hover {
  color: rgb(132, 132, 132);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
  align-items: center;
}
@media (min-width: 992px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  align-items: center;
}
@media (min-width: 992px) {
  .navbar {
    width: auto;
    flex-direction: row;
  }
}
.navbar__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000000;
}
.navbar__link--sign {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navbar__link--sign:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navbar__link--sign {
    width: auto;
  }
}

@media (min-width: 768px) {
  .form__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.form__cont {
  background-color: #e8505b;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.form__title {
  color: #FFFFFF;
  text-align: center;
}
.form__field {
  margin-bottom: 1rem;
}
.form__label {
  display: block;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 1rem 0;
}
.form__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.form__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  text-transform: uppercase;
}
.form__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .form__submit {
    width: 100%;
  }
}
.form__img--container {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}
.form__img {
  width: 30rem;
}
@media (min-width: 992px) {
  .form__img {
    width: 100%;
  }
}

.instructions {
  margin-top: 5rem;
}
.instructions__title {
  text-align: center;
  color: #FFFFFF;
}
.instructions__title--span {
  font-size: 1.8rem;
  display: block;
}
.instructions__content {
  background-color: #e8505b;
  margin-top: -1px;
}
@media (min-width: 768px) {
  .instructions__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.instructions__img {
  transition: 0.3s;
}
.instructions__img:hover {
  transform: rotate(-4deg) scale(1.2);
}

.testimonials {
  padding: 5rem 0;
}
.testimonials__title {
  text-align: center;
}
@media (min-width: 992px) {
  .testimonials__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 2rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/5;
  }
  .testimonial:nth-child(2) {
    grid-column: 1/5;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/6;
  }
}
.testimonial__header {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.testimonial__img {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonial__author {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 3rem;
  margin: 0.5rem 0;
}
.testimonial__text {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0;
  padding-left: 4rem;
  position: relative;
}
.testimonial__text::before {
  content: '"';
  font-size: 10rem;
  position: absolute;
  top: -4rem;
  left: 0;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.favorites {
  background-color: #FFFFFF;
  padding: 5rem 0;
}
.favorites__title {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favorites__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.favorite {
  background-color: #eceeef;
  padding: 4rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 1.8rem -0.8rem #657177;
  border-radius: 1rem;
}
.favorite:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorite {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .favorite__grid {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 992px) {
  .favorite__grid {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 4rem;
  }
}
.favorite__img {
  max-width: 20rem;
  margin: 0 auto 2rem auto;
}
.favorite__contet {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favorite__contet {
    align-items: flex-start;
  }
}
.favorite__title {
  margin: 1rem 0;
  color: #e8505b;
  font-size: 2.4rem;
}
.favorite__text {
  margin: 0.5rem 0;
  text-align: center;
  font-size: 1.6rem;
  color: #455a64;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .favorite__text {
    text-align: left;
  }
}

.delivery {
  padding: 5rem 0;
}
.delivery__title {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .delivery__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.delivery__contet {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.delivery__text {
  line-height: 1.5;
  color: #455a64;
}
.delivery__link {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.delivery__link:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .delivery__link {
    width: auto;
  }
}

.downloads {
  background-color: #FFFFFF;
}
.downloads__grid {
  display: flex;
  flex-direction: column-reverse;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .downloads__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.downloads__img {
  margin: 5rem auto 0 auto;
  width: 50%;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .downloads__img {
    margin-top: 0;
  }
}
.downloads__img:hover {
  transform: rotate(4deg) scale(1.2);
}

.shops {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .shops__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.shops__title {
  color: #455a64;
  margin: 2rem 0 0 0;
}
.shops__text {
  margin-bottom: 1rem;
}
.shops__title, .shops__text {
  text-align: center;
}
.shops__link {
  transition: 0.5s;
}
.shops__link:hover {
  transform: scale(1.1);
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
.footer__box {
  margin-bottom: 5rem;
}
.footer__box:last-of-type {
  margin-bottom: 0;
}
.footer__title {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__title {
    text-align: left;
  }
}
.footer__nav {
  display: flex;
  flex-direction: column;
}
.footer__nav, .footer__text {
  margin-top: 2rem;
}
.footer__link, .footer__text {
  font-size: 2rem;
  color: #FFFFFF;
  text-align: center;
  line-height: 1.8;
}
@media (min-width: 768px) {
  .footer__link, .footer__text {
    text-align: left;
  }
}
.footer__copyright {
  background-color: #e8505b;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 1rem;
  font-weight: bold;
}