@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: v.$secondary;
    padding-top: 5rem;

    &__grid{
        @include m.tablet {
            @include m.grid(3, 1rem) ;
        }
    }
    &__box{
        margin-bottom: 5rem;
        &:last-of-type{
            margin-bottom: 0;
        }
    }
    &__title{
        color: v.$white;
        margin: 0;
        text-align: center;

        @include m.tablet {
            text-align:left ;
        }
    }
    &__nav{
        display: flex;
        flex-direction: column;
    }
    &__nav,
    &__text{
        margin-top: 2rem;
    }
    &__link,
    &__text{
        font-size: 2rem;
        color: v.$white;
        text-align: center;
        line-height: 1.8;

        @include m.tablet {
            text-align: left;
        }
    }
    &__copyright{
        background-color: v.$primary;
        text-align: center;
        color: v.$white;
        margin: 0;
        padding: 1rem;
        font-weight: bold;
    }
}