@use 'base/mixins' as m;
@use 'base/variables' as v;

.delivery{
    padding: 5rem 0;

    &__title{
        text-align: center;
        color: v.$secondary;
    }

    &__grid{
        @include m.tablet {
            @include m.grid(2, 5rem) ;
        }
    }
    &__contet{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: flex-start;
    }
    &__text{
        line-height: 1.5;
        color: v.$secondary;
    }
    &__link{
        @include m.btn(v.$primary, v.$white)
    }
}