@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    display: flex;
    flex-direction: column;
    padding: 5rem 0;
    align-items: center;

    @include m.desktop {
        flex-direction: row;
        justify-content: space-between;
        
    }

    &__logo{
        width: 30rem;
        margin-bottom: 5rem;

        @include m.tablet {
            margin-bottom:0;
        }
    }
}