@use 'base/variables' as v;
@use 'base/mixins' as m;

.favorites{
    background-color: v.$white;
    padding: 5rem 0;

    &__title{
        text-align: center;
        color: v.$secondary;
    }

    &__grid{
        @include m.tablet {

            @include m.grid(2, 4rem) ;
        }
    }
}