@use 'base/variables' as v;
@use 'base/mixins' as m;

.favorite{
    background-color: v.$ligthGray;
    padding: 4rem;
    margin-bottom: 2rem;
    box-shadow: 0 0 1.8rem -0.8rem darken(v.$ligthGray, $amount: 50%);
    border-radius: 1rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include m.tablet {
        margin-bottom: 0;
    }

    &__grid{
        @include m.tablet {
            display: grid;
            grid-template-columns: 2fr 3fr;
            gap: 2rem;
            align-items: center;
        }
        @include m.desktop {
            display: grid;
            grid-template-columns: 1fr 3fr;
            gap: 4rem;
        }
    }
    &__img{
        max-width: 20rem;
        margin: 0 auto 2rem auto;
    }
    &__contet{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            align-items: flex-start;
        }
    }
    &__title{
        margin: 1rem 0;
        color: v.$primary;
        font-size: 2.4rem;
    }
    &__text{
        margin: .5rem 0;
        text-align: center;
        font-size: 1.6rem;
        color: v.$secondary;
        line-height: 1.5;

        @include m.tablet {
            text-align: left;
        }
    }
}